const initialState = {
	debug: false,
	sideBox: {},
	popupBox: {},
	message: {},
	notifications: {},
	suggestions: {}
};

export default function layoutReducer(oldState = initialState, action = {}) {
	switch (action.type) {

		case 'layout/sideBox':
			{
				//
				if (!action.data || !action.data.Box) {
					// replace only show/hide property (when hiding, so content does not go away, only the UI hides)
					oldState.sideBox.opened = false;
				} else {
					// replace entire sidemenu (first init OR replace existing with new instance)
					oldState.sideBox = action.data;
					oldState.sideBox.opened = true;
				}
				// =
				let state = Object.assign({}, oldState);
				return state;
			}
		case 'layout/popupBox':
			{
				//
				if (!action.data || !action.data.Box) {
					// replace only show/hide property (when hiding, so content does not go away, only the UI hides)
					oldState.popupBox.opened = false;
				} else {
					// replace entire sidemenu (first init OR replace existing with new instance)
					oldState.popupBox = action.data;
					oldState.popupBox.opened = true;
				}
				// =
				let state = Object.assign({}, oldState);
				return state;
			}


		case 'layout/reboot':
			{
				//
				oldState.suggestions.reboot = true;
				oldState.notification = {
					message: "You must reboot",
					support: "for changes to take effect"
				};
				// =
				let state = Object.assign({}, oldState);
				return state;
			}
		case 'layout/rebootX':
			{
				//
				oldState.suggestions.reboot = false;
				oldState.notification = {};
				// =
				let state = Object.assign({}, oldState);
				return state;
			}
			
		case 'layout/notification':
			{
				//
				oldState.suggestions.reboot = false;
				oldState.notification = {
					message: "Firmware upgrade available",
					support: "Please click here to update your device"
				};
				// =
				let state = Object.assign({}, oldState);
				return state;
			}
		case 'layout/notificationX':
			{
				//
				oldState.suggestions.reboot = false;
				oldState.notification = {};
				// =
				let state = Object.assign({}, oldState);
				return state;
			}


		case 'layout/message':
			{
				if (!action.data.message && !oldState.message.message) { 
					return oldState;
				}
				let state = Object.assign({}, oldState);
				// open
				if (action.data.message) {
					// use latest message expiration time
					let newExpiry = Date.now() + 5000;
					let oldExpiry = oldState.message.expires;
					if (oldExpiry && oldExpiry > newExpiry) {
						newExpiry = oldExpiry;
					}
					state.message = action.data;
					state.message.expires = newExpiry;
					state.message.opened = true;
					state.message.active = true;
					// status ~= type
					if (state.message.status===undefined) {
						state.message.type = "notice";
					} else if (state.message.status===0) {
						state.message.type = "success";
					} else {
						state.message.type = "error";
					}
				// close
				} else {
					state.message = {};
				}
				// =
				return state;
			}
		case 'layout/messageX':
			{
				let state = Object.assign({}, oldState);
				//
				state.message.expires = Date.now() + (action.data.extendTime||5000);
				state.message.opened = false;
				state.message.active = true;
				// =
				return state;
			}
		case 'layout/messageTimeout':
			{
				let state = Object.assign({}, oldState);
				// only act if necessary
				if (oldState.message.opened && oldState.message.expires <= Date.now()) {
					// message expired
					state.message.expires = Date.now() + (action.data.extendTime||5000);
					state.message.opened = false;
					state.message.active = true;
				}
				return state;
			}
		case 'layout/messageExtend':
			{
				let state = Object.assign({}, oldState);
				//
				state.message.expires = Date.now() + (action.data.extendTime||5000);
				state.message.opened = true;
				state.message.active = true;
				// =
				return state;
			}

		default:
			{
				return oldState;
			}
	}
}



