import React from 'react';
import { connect } from 'react-redux';

import * as layoutActions from '@src/states/layout/actions';

import '@src/components/layout/popup.scss';

class Popup extends React.PureComponent {

	render() {
		const { layout, dispatch } = this.props;

		return (
			<div className={ "popupBox" + ( layout.popupBox.opened===true ? ' opened' : '' ) }>
				<div className="popupBox_overlay" onClick={()=>{ dispatch( layoutActions.popupBox() )}} />
				<div className="popupBox_content">
					<div className="popupBox_close">
						<span className="fontIcon icon-cross" title="Close" onClick={()=>{ dispatch( layoutActions.popupBox() )}} />
					</div>

					{layout.popupBox.Box}

				</div>
			</div>
		)
	}
}

const mapStateToProps = (state) => {
    return {
    	layout: state.layout
    };
};
export default connect(mapStateToProps)(Popup);


