/* core */
import React from 'react';
import { connect } from 'react-redux';
/* components */
import Box from '@src/components/box/Box';
import Layout from '@src/components/layout/Layout';
/* forms */
import LoginForm from '@src/components/user/FormLogin';
import "@src/pages/user/PageLogin.scss";


/*
	form
*/
class PageComponent extends React.Component {


	render() {

		const { dispatch } = this.props;


		var HelpBox = <Box box={{title:"Login Help"}}>
			<div className="form">
				<p>Username: root<br />Password: admin</p>
			</div>
		</Box>;
		const TopChildren = [
			<span key="Login Help" className="fontIcon icon-pk_question" onClick={()=>{ dispatch( { type: 'layout/sideBox', data: {content:HelpBox} } )}}></span>
		];


		return (
			<Layout router={this.props.router} route={this.props.route} pages={false}>
				
				<Box box={{title:this.props.route.page.title, id:"boxLogin", TopChildren:TopChildren}}>
					
					<LoginForm router={this.props.router} />

				</Box>

			</Layout>
		)
	}

};


const mapStateToProps = (state) => {
	return {};
};
export default connect(mapStateToProps)(PageComponent);


