import React from 'react';
import { connect } from 'react-redux';

import * as layoutActions from '@src/states/layout/actions';

import '@src/components/layout/side.scss';

class Side extends React.PureComponent {

	render() {
		const { layout, dispatch } = this.props;
		
		return (
			<div className={ "sideBox" + ( layout.sideBox.opened===true ? ' opened' : '' ) }>
				<div className="sideBox_overlay" onClick={()=>{ dispatch( layoutActions.sideBox() )}} />
				<div className="sideBox_content">
					<div className="sideBox_close" onClick={()=>{ dispatch( layoutActions.sideBox() )}}>
						<span className="fontIcon icon-cross" title="Close" />
					</div>

					{layout.sideBox.Box}

				</div>
			</div>
		)
	}
}

const mapStateToProps = (state) => {
    return {
    	layout: state.layout
    };
};
export default connect(mapStateToProps)(Side);


