/* core */
import React from 'react';
import { connect } from 'react-redux';
/* components */
import Box from '@src/components/box/Box';
import Layout from '@src/components/layout/Layout';
/* forms */
import { LuxulForm, LuxulSelect, validations } from '@src/components/form/LuxulForm';


/*
	form
*/
class PageComponent extends React.Component {

	constructor() {
		super();
		this.state = {
			luxulFormValues: {
				initial_choice: ""
			}
		};
	}

	render() {

		var handleSubmit = (promise) => {
			// form submit attempted. Don't do anything.
		};

		return (
			<Layout router={this.props.router} route={this.props.route}>
				
				<Box box={{title:this.props.route.page.title}}>
					
					<LuxulForm 
					stateScope={this} 
					onSubmit={handleSubmit}
					>

						<div className="formSection">
							<h4>LuxulSelect component:</h4>
							<fieldset>
								<label>Choose one:</label>
								<LuxulSelect stateScope={this} name="initial_choice" validations={[validations.required]}>
									<option value="">Select...</option>
									<option value="Selection 1">Selection 1</option>
									<option value="Selection 2">Selection 2</option>
									<option value="Selection 3">Selection 3</option>
								</LuxulSelect>
							</fieldset>
						</div>

						{this.state.luxulFormValues.initial_choice==="Selection 1" && 
							<div className="formSection">
								<h4>Cool! You chose the first option.</h4>
								<fieldset>
									<label>Now you can choose something more specific:</label>
									<LuxulSelect stateScope={this} name="secondary_choice">
										<option value="">Select...</option>
										<option value="1.1">1.1</option>
										<option value="1.2">1.2</option>
										<option value="1.3">1.3</option>
									</LuxulSelect>
								</fieldset>
							</div>
						}
						{this.state.luxulFormValues.initial_choice==="Selection 2" && 
							<div className="formSection">
								<h4>You chose the second option.</h4>
								<fieldset>
									<label>Now if you want, try choosing a more specific:</label>
									<LuxulSelect stateScope={this} name="secondary_choice">
										<option value="">Select...</option>
										<option value="2.1">2.1</option>
										<option value="2.2">2.2</option>
										<option value="2.3">2.3</option>
									</LuxulSelect>
								</fieldset>
							</div>
						}
						{this.state.luxulFormValues.initial_choice==="Selection 3" && 
							<div className="formSection">
								<h4>You chose the third and last option...</h4>
								<fieldset>
									<label>Now optionally, lets make a secondary selection to narrow it down even more:</label>
									<LuxulSelect stateScope={this} name="secondary_choice">
										<option value="">Select...</option>
										<option value="3.1">3.1</option>
										<option value="3.2">3.2</option>
										<option value="3.3">3.3</option>
									</LuxulSelect>
								</fieldset>
							</div>
						}

					</LuxulForm>
					
				</Box>

			</Layout>
		)
	}

};


const mapStateToProps = (state) => {
	return {};
};
export default connect(mapStateToProps)(PageComponent);


