/* core */
import React from 'react';
import { connect } from 'react-redux';
/* components */
import Box from '@src/components/box/Box';
import Layout from '@src/components/layout/Layout';
/* forms */
import { LuxulForm, LuxulToggle, validations } from '@src/components/form/LuxulForm';


/*
	form
*/
class PageComponent extends React.Component {

	constructor() {
		super();
		this.state = {
			luxulFormValues: {
				first_thing: false,
				second_thing: false
			}
		};
	}

	render() {

		var handleSubmit = (promise) => {
		};
		
		return (
			<Layout router={this.props.router} route={this.props.route}>
				
				<Box box={{title:this.props.route.page.title}}>
					
					<LuxulForm 
					stateScope={this} 
					onSubmit={handleSubmit}
					>

						<div className="formSection">
							<h4>LuxulToggle component:</h4>
							<fieldset>
								<label>First thing:</label>

								{/*
									then simply add a validation to your input element
								*/}
								<LuxulToggle stateScope={this} name="first_thing" labelOn="ON" labelOff="OFF" validations={[validations.required]} />
							</fieldset>
						</div>

						<div className="formSection">
							<fieldset>

								{/*
									a bit different from the LuxulInput though, LuxulToggle only supports the "required" validation. Guess why that is?
								*/}
								<LuxulToggle stateScope={this} name="second_thing" labelOff="Second thing: " validations={[validations.required]} />
							</fieldset>
						</div>

					</LuxulForm>

				</Box>

			</Layout>
		)
	}

};


const mapStateToProps = (state) => {
	return {};
};
export default connect(mapStateToProps)(PageComponent);


