import React from 'react';
import { connect } from 'react-redux';

// import * as layoutActions from '@src/states/layout/actions';

import Box from '@src/components/box/Box';
import Messages from '@src/components/layout/Messages';

import '@src/components/layout/top.scss';

class Top extends React.PureComponent {

	render() {
		const { dispatch, router } = this.props;

		var DeviceInfo = <Box box={{title:"New Luxul Product", theme:"global"}}>
			<div className="form">
				<p>Model #: {window.device.info.model}</p>
				<p>Firmware #: {window.device.info.firmware}</p>
			</div>
		</Box>;

		return (
			<div className="top">
				<Messages router={router} />
				
				<span className="logo" onClick={()=>{ router.push('/login'); }}>
					<img src="/branding/logo/logo_white.svg" alt="LUXUL" />
				</span>
								
				<span className="details">
					<span className="link" onClick={()=>{ dispatch( { type: 'layout/sideBox', data: {Box:DeviceInfo} } )}}>
						<span className="title" style={{textIndent:"-1.5px"}}><span className="fontIcon icon-carat_down" /> {window.device.info.model}</span>
						<span className="text" style={{textIndent:"-1px"}}>Firmware: {window.device.info.firmware}</span>
					</span>
				</span>
				
			</div>
		)
	}
}

const mapStateToProps = (state) => {
    return {
    	layout: state.layout
    };
};
export default connect(mapStateToProps)(Top);


