import $ from "jquery";
import React from 'react';
import { connect } from 'react-redux';

// import * as layoutActions from '@src/states/layout/actions';

import '@src/components/layout/nav.scss';

class NavComponent extends React.PureComponent {


	/*
		scrollHorizontally
		".scrollHorizontally" is the scrollable parent element... it receives a className="scrollEnd" or scrollStart... do whatever with css
	*/ 
	componentDidMount() {
		if (this.refs.scrollHorizontally) {

			// on initial load
			// wait until after children have rendered 
			// this way of setTimout bind(this) is a hack, should find a better way!
			setTimeout(function(){

				// initially auto-scroll to page
				// this is glitchy! works fine, but blinks when rendered - because it is in a timeout
				if (this.refs.navItemSelected && this.refs.navItemSelected.offsetLeft > (this.refs.scrollHorizontally.offsetWidth-this.refs.navItemSelected.offsetWidth)) {
					this.refs.scrollHorizontally.scrollLeft = this.refs.navItemSelected.offsetLeft;
				}
				// initially show correct arrow
				this.scrollHorizontallyEnter(this.refs.scrollHorizontally);

			}.bind(this),0);
			// on scroll
			this.refs.scrollHorizontally.addEventListener("scroll", this.scrollHorizontallyEnter);

		}
	}
	componentWillUnmount() {
		if (this.refs.scrollHorizontally) {

			// clean up
			this.refs.scrollHorizontally.removeEventListener("scroll", this.scrollHorizontallyEnter);

		}
	}
	scrollHorizontallyEnter = (element)=>{
		/*
			find element
		*/
		// container element is target
		if (element.target) {
			// so we can pass to this the element or the parent object of the element
			element = element.target;
		}
		/*
			then use it
		*/
		// to show tip only when at the top... 
		if (element.scrollLeft > 20) {
			element.classList.add('scrollLeft');
		} else {
			element.classList.remove('scrollLeft');
		}
		if ((element.scrollLeft + element.offsetWidth + 20) < (element.scrollWidth)) {
			element.classList.add('scrollRight');
		} else {
			element.classList.remove('scrollRight');
		}
	}


	render() {

		const { router } = this.props;
		const pathname = router.location.pathname;
		const NavLinks = [];

		for (let i = 0; i < window.device.routes.length; i++) {

			const pageLink = window.device.routes[i];
			const pageTabs = pageLink.children ||[];
			const NavTabs = [];
			var isPageSelected = pathname === pageLink.url;

			// tabs
			for (let t = 0; t < pageTabs.length; t++) {
				if (pageTabs[t] && !pageTabs[t].hidden) {
					let tab = pageTabs[t];
					var isTabSelected = pathname === tab.url;
					if (isTabSelected) {
						isPageSelected = true;
					}

					NavTabs.push(
						<div key={ "NavTabs"+t } className={ "tab link" + (isTabSelected ? ' selected' : '') } onClick={function(){ router.push( tab.url ) }}>
							{tab.tabTitle || tab.title}
						</div>
					);
				}
			}

			// links
			const isHaveTabs = NavTabs.length>1;
			NavLinks.push(
				<div key={"Nav_"+pageLink.url}  ref={(isPageSelected ? 'navItemSelected' : '')} className={ 'navItem link' + (isPageSelected ? ' selected' : '') + (isHaveTabs ? ' withTabs' : '') }>
					<div className="navItemTitle">

						<div className={ "link " + ((isPageSelected) && " navItemTitleFirst") } onClick={ (e)=>{ if (pageLink.url){ router.push(pageLink.url); } e.preventDefault(); } }>
							{pageLink.icon && 
								<span className={"fontIcon icon-"+pageLink.icon}></span>
							}
							<span>{pageLink.tabTitle || pageLink.title}</span>
							{(isHaveTabs && isPageSelected) && 
								<span className="colon fontIcon icon-carat_down"></span>
							}
						</div>

						{isHaveTabs &&
							<div className="nav1Tabs">
								{NavTabs}
							</div>
						}

					</div>
				</div>
			);

		};

		// ok
		return (
			<div className="nav  nav_left">
				<div ref="scrollHorizontally" className="navItems">
					
					{/* mimicks the markup of real links - that way the padding and heights will match up*/}
					<div className="navItem link fixToTop" ref={(e)=>{ this.e = e; }} onClick={()=>{ $(this.refs.scrollHorizontally).animate({ scrollLeft: 0}, 666); }}>
						{/* except this span, which is position:Absolute */}
						<span className="absoluteIcon fontIcon icon-carat_left"></span>
						<div className="navItemTitle">
							<div className="link">
								<span className="fontIcon icon-"></span>
								<span>&nbsp;</span>	
							</div>
						</div>
					</div>
					<div className="navItem link fixToTop" ref={(e)=>{ this.e = e; }} onClick={()=>{ $(this.refs.scrollHorizontally).animate({ scrollLeft: $(this.refs.scrollHorizontally).prop("scrollWidth")}, 666); }}>
						{/* except this span, which is position:Absolute */}
						<span className="absoluteIcon fontIcon icon-carat_right"></span>
						<div className="navItemTitle">
							<div className="link">
								<span className="fontIcon icon-"></span>
								<span>&nbsp;</span>	
							</div>
						</div>
					</div>

					{/* the real links */}
					{NavLinks}

				</div>
			</div>
		)
	}
}

const mapStateToProps = (state) => {
    return {
    	layout: state.layout
    };
};
export default connect(mapStateToProps)(NavComponent);


