/* core */
import React from 'react';
import { connect } from 'react-redux';
/* components */
import Box from '@src/components/box/Box';
import Layout from '@src/components/layout/Layout';
/* forms */
import { LuxulForm, LuxulInput, LuxulHint } from '@src/components/form/LuxulForm';


/*
	form
*/
class PageComponent extends React.Component {

    constructor() {
        super();
        this.state = {
            luxulFormValues: {
                first_text_field: "",
                second_text_field: ""
            }
        };
    }

	render() {

		return (
			<Layout router={this.props.router} route={this.props.route}>
				
				<Box box={{title:this.props.route.page.title}}>
					
					<LuxulForm
			            stateScope={this}
			            >
			 
			                <div className="formSection">
			                  	{/*
			                        Here's the hint tag:
			                    */}
			                    <h4>
			                    	LuxulHint component:
			                    	<LuxulHint stateScope={this} title="About LuxulHint:" description={<span><p>You can place the LuxulHint anywhere you want the little question mark circle icon to appear.</p><p>Click on that little icon and a box will drop down, containing this text, or whatever text you include in the description field here.</p></span>} />
			                    </h4>
			                    <fieldset>
			                        {/*
			                            Here's the hint tag:
			                        */}
			                        <label>The hint is usually placed in or near the label, before the input...
			                            <LuxulHint stateScope={this} title="About LuxulHint:" description={<span><p>Though technically, you can place the LuxulHint anywhere you want the little question mark circle icon to appear.</p><p>Click on that little icon and a box will drop down, containing this text, or whatever text you include in the description field here.</p></span>} />
			                        </label>
			                    </fieldset>
			                </div>

			                <div className="formSection">
			                    <fieldset>
			                        <LuxulInput stateScope={this} name="first_text_field" type="text" placeholder="First thing..." />
			                    </fieldset>
			                </div>

			                <div className="formSection">
			                    <fieldset>
			                        <LuxulInput stateScope={this} name="second_text_field" type="text" placeholder="Second thing..." />
			                    </fieldset>
			                </div>
			 
			            </LuxulForm>
			            
				</Box>

			</Layout>
		)
	}

};


const mapStateToProps = (state) => {
	return {};
};
export default connect(mapStateToProps)(PageComponent);


