import React from 'react';
import { connect } from 'react-redux';

import * as layoutActions from '@src/states/layout/actions';
import * as userActions from '@src/states/user/actions';

import { LuxulForm, LuxulInput, validations } from '@src/components/form/LuxulForm';

class FormLogin extends React.Component {

	constructor() {
		super();
		this.state = {
			luxulFormButtons: false,
			luxulFormValues: {
				username:"",
				password:""
			}
		}
	}

	componentDidMount() {
		const { dispatch } = this.props;
		// always log out if on this page
		dispatch( layoutActions.message({}) );
		dispatch( userActions.logout({}) )
		.catch((err)=>{ 
			dispatch(layoutActions.message(err));
		});
	}

	render() {

		var { dispatch, router } = this.props;

		var formSubmit = (promise) => {
			this.setState({"luxulFormSubmitting": true});

			dispatch(layoutActions.message({}));
			promise
			.then((event)=>{
				// form is VALID
				console.log('this.state.luxulFormValues',this.state.luxulFormValues);

				// OK, do custom action
				dispatch(userActions.login(this.state.luxulFormValues))
				.then((action)=>{
					router.push('/');
					// dispatch(layoutActions.message({status:0, message:"Welcome!"}));
				})
				.catch((err)=>{ 
					dispatch(layoutActions.message({message:"Login failed. Please check that your device is connected and powered on.", status:1}));
					this.setState({"luxulFormSubmitting": false});
				});

			})
			.catch((err)=>{
				// form is NOT valid
				console.error("Error: "+err);
			});
		};

		return (
						
			<LuxulForm 
			stateScope={this} 
			onSubmit={formSubmit}
			>
				
				<div className="formSection">
					<fieldset>
						<label>Username:</label>
						<LuxulInput stateScope={this} name="username" type="text" validations={[validations.required]} />
					</fieldset>
					<fieldset>
						<label>Password:</label>
						<LuxulInput stateScope={this} name="password" type="password" validations={[validations.required]} />
					</fieldset>
				</div>

				<div className="formSection">
					<button className={((this.state.luxulFormSubmitting || (this.state.luxulFormInvalid && this.state.luxulFormInvalid.length)) ? " " : " primary")} type="submit"><b>Enter</b></button>
				</div>

			</LuxulForm>
		)
	}

};

const mapStateToProps = (state) => {
	return {};
};
export default connect(mapStateToProps)( FormLogin );



