import React from 'react';
import { connect } from 'react-redux';

import { Link } from 'react-router';

import '@src/components/layout/bottom.scss';

class Bottom extends React.PureComponent {

	render() {
		const { user } = this.props;

		return (
			<div className="bottom">

				<span className="nav_left logo">
					<img src="/branding/logo/legrand_onDark3.svg" alt="Legrand" />
				</span>

				<span className="nav_center">Copyright &copy;2017 Luxul</span>

				{ user.loggedIn 
				? 
				<span className="nav_right"><Link href="/login"><span className="fontIconText">logout</span> <span className="fontIcon icon-ui_security" /></Link></span>
				:
				""
				}

			</div>
		)
	}
}

const mapStateToProps = (state) => {
    return {
    	user: state.user
    };
};
export default connect(mapStateToProps)(Bottom);


